.navbar
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 4rem
    background-color: #fff
    z-index: 100
    display: flex
    justify-content: space-between
    align-items: center
    padding: 0 2rem
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1)
    &__container
        display: flex
        justify-content: space-between
        align-items: center
        width: 100%
    &__logo
        font-size: 1.5rem
        font-weight: 700
        color: #000
        text-decoration: none
        &__button
            background-color: transparent
            border: none
            font-size: 1rem
            font-weight: 700
            color: #000
            cursor: pointer
            outline: none
            transition: all 0.3s ease-in-out
            &:hover
                color: #000
                transform: scale(1.1)
            &:active
                transform: scale(0.9)
    &__menu
        display: flex
        justify-content: space-between
        align-items: center
        width: 30%
        &--link
            color: #000
            text-decoration: none
        &--link-active
            color: #000
            text-decoration: none
            border-bottom: 2px solid #000
        &__list
            display: flex
            justify-content: space-between
            align-items: center
            width: 100%
            list-style: none
            padding: 0
            margin: 0
    &__item
        margin: 0 1rem
        &__button
            background-color: transparent
            border: none
            font-size: 1rem
            font-weight: 700
            color: #fff
            cursor: pointer
            outline: none
            transition: all 0.3s ease-in-out
            &:hover
                color: #fff
                transform: scale(1.1)
            &:active
                transform: scale(0.9)

.hero
    width: 100%
    height: 100vh
    &__content
        height: 100%
        width: 100%
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
    &__info
        color: #fff
        font-size: 6rem
        font-weight: 500
        text-align: center
        color: #c9c9c9
    &__avatar
        width: 82.48px
        height: 298.95px

.banner
    width: 100%
    height: 100vh
    &__content
        height: 100%
        width: 100%
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
    &__info
        color: #fff
        font-size: 1.5rem
        font-weight: 300
    &__avatar
        width: 82.48px
        height: 298.95px

.timeline
    width: 100%
    &__container
        width: 100%
    &__item
        width: 100%
        display: flex
        justify-content: center
        align-items: center
        padding: 2rem

        &__content
            width: 30%
            display: flex
            justify-content: center
            align-items: center
            flex-direction: column
            gap: 2rem

.experiences
    width: 100%
    &__reveal
        display: flex
        justify-content: center
        align-items: center
        flex-direction: column
    &__time, &__card__title
        width: 100%
        font-size: 2rem
        font-weight: 500
        color: #c9c9c9
    &__card
        width: 100%
        height: 100%
        display: flex
        flex-direction: column
        justify-content: center
        padding: 2rem
        border-radius: 0.5rem
        border: 1px solid #5f5f5f
        box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1)
        transition: all 0.3s ease-in-out
        &__line
            width: 100%
            border: 1px solid #5f5f5f
            margin: 1rem 0
        &__company
            font-size: 1.2rem
            font-weight: 400
            color: #c9c9c9
        &__description
            font-size: 1rem
            font-weight: 300
            color: #c9c9c9
            margin-top: 1rem
            
.footer
    width: 100%
    padding: 1rem
    background-color: #000
    display: flex
    justify-content: space-between
    align-items: center
    flex-direction: column
    &__line
        width: 100%
        border: 1px solid #5f5f5f
        margin: 1rem 0
    &__content
        width: 100%
        display: flex
        justify-content: space-between
        align-items: center
        &__title
            font-size: 1rem
            font-weight: 700
            color: #fff
            text-decoration: none
        &__links
            display: flex
            justify-content: space-between
            align-items: center
            width: 30%
            list-style: none
            padding: 0
            margin: 0
            & >a
                color: #fff
                text-decoration: none
                margin: 0 1rem
                font-size: 1rem
                font-weight: 700
                transition: all 0.3s ease-in-out
                &:hover
                    transform: scale(1.1)
                &:active
                    transform: scale(0.9)
            & img
                filter: invert(1)
                width: 1.5rem
                height: 1.5rem
                transition: all 0.3s ease-in-out
                &:hover
                    transform: scale(1.1)
                &:active
                    transform: scale(0.9)

